* {
    box-sizing: border-box;
}

html {
    font-size: 18px;
    // color: $clr2;
}

body {
    font-family: livvic-thin, sans-serif;
    margin: 0;
    overflow-y: auto;
    background-color: $clr1;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: normal;
    line-height: 1;
    margin: 0 0 0.6em;
}

a {
    text-decoration: none;
    color: rgb(22, 22, 22);
    cursor: pointer;
    &.active {
        color: cornflowerblue;
    }
}
button {
    cursor: pointer;
    transition: 0.3s;
    &:hover {
        opacity: 0.7;
    }
}
