.success-stories {
    position: relative;
    min-height: rem(700px);
    min-width: 100%;
    background-color: $clr1;
    .stories-img-top {
        position: absolute;
    }
    .stories-img-bottom {
        position: absolute;
        bottom: 0;
        right: 0;
    }
    .success-grid {
        align-content: center;
        grid-template-rows: 1fr 2fr;
        max-height: 75%;
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        .success-grid-title {
            /* Style for "Delivering" */
            width: 100%;
            height: 93px;
            color: #ffffff;
            font-family: livvic-bold;
            font-size: 48px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 48px;
            text-align: center;
        }
        .success-grid-stories {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
