.story {
    background-image: url("../icon-quotes.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: top;
    & > *:not(:last-child) {
        margin-bottom: rem(10px);
    }
    .a-story {
        /* Style for "â€œThe team" */
        width: 350px;
        height: 97px;
        color: #ffffff;
        font-family: livvic-semibold;
        font-size: 15px;
        font-weight: 600;
        font-style: normal;
        letter-spacing: normal;
        line-height: 25px;
        text-align: center;
    }
    .owner-name {
        /* Style for "Kady Baker" */
        width: 350px;
        height: 28px;
        color: #79c8c7;
        font-family: livvic-semibold;
        font-size: 18px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: normal;
        line-height: 28px;
        text-align: center;
    }
    .owner-title {
        /* Style for "Product Ma" */
        width: 350px;
        height: 18px;
        color: #ffffff;
        font-family: livvic-medium;
        font-size: 13px;
        font-weight: 500;
        font-style: normal;
        letter-spacing: normal;
        line-height: 18px;
        text-align: center;
    }
    .owner-img {
        align-self: center;
        max-height: rem(62px);
        max-width: rem(62px);
        border: 2px solid #c4fffe;
        border-radius: rem(62px);
    }
}
