.card {
    width: 100%;
    min-height: rem(700px);
    position: relative;
    .card-title {
        position: relative;
        justify-content: space-evenly;
        left: rem(-200px);
        height: 100%;
        p {
            /* Style for "Find the b" */
            width: 635px;
            width: 55%;
            height: 193px;
            color: #ffffff;
            font-family: livvic-bold;
            font-size: 100px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: normal;
            line-height: 100px;
            text-align: left;
            /* Text style for "talent" */
        }
        .card-title p::after {
            content: "talent";
            color: #f67e7e;
            font-style: normal;
            letter-spacing: normal;
            line-height: normal;
        }
    }
    .card-description {
        height: 100%;
        width: 25%;
        p {
            /* Style for "Finding th" */
            width: 445px;
            height: 110px;
            color: #ffffff;
            font-family: livvic-thin;
            font-size: 18px;
            font-weight: 600;
            font-style: normal;
            letter-spacing: normal;
            line-height: 28px;
            text-align: left;
        }
        .img-bottom {
            position: absolute;
            bottom: 0;
        }
    }
}
